<template>
  <div class="row">
    <div>
      <b-button
        @click="show_popup_them_moi = true"
        variant="success"
        class="float-right"
        >Thêm mới</b-button
      >
      <b-modal v-model="show_popup_them_moi" title="Thêm mới" size="lg">
        <themmoi />
        <template #modal-footer>
          <div class="w-100">
            <b-button
              variant="primary"
              size="sm"
              class="float-right"
              @click="show_popup_them_moi = false"
            >
              Đóng
            </b-button>
          </div>
        </template>
      </b-modal>
      <b-modal v-model="show_popup_sua" title="Cập nhật hóa đơn" size="lg">
        <sua :chitiethoadon_item="chitiet_hoadon_item_selected" />
        <template #modal-footer>
          <div class="w-100">
            <b-button
              variant="primary"
              size="sm"
              class="float-right"
              @click="show_popup_sua = false"
            >
              Đóng
            </b-button>
          </div>
        </template>
      </b-modal>
    </div>
    <div class="col-md-12">
      <p class="h5 font-weight-bold text-center">Danh sách</p>
      <b-form-group label="Tìm kiếm" label-for="filter-input" class="my-2">
        <b-input-group size="sm">
          <b-form-input
            id="filter-input"
            v-model="filter"
            type="search"
            placeholder="Nhập từ khóa để tìm kiếm"
          ></b-form-input>
          <b-input-group-append>
            <b-button :disabled="!filter" @click="filter = ''">Xóa</b-button>
          </b-input-group-append>
        </b-input-group>
      </b-form-group>
      <b-table
        striped
        hover
        :items="danh_sach_chi_tiet_hoa_don"
        :fields="fields_table"
        :filter="filter"
        :per-page="perPage"
        :current-page="currentPage"
        id="my-table"
        class="table-hoadon"
      >
        <template #cell(giasi)="data">
          {{ formatMoney(data.item.giasi, 0, 2) }} VNĐ
        </template>
        <template #cell(giack)="data">
          {{ formatMoney(data.item.giack, 0, 2) }} VNĐ
        </template>
        <template #cell(gianhap)="data">
          {{ formatMoney(data.item.gianhap, 0, 2) }} VNĐ
        </template>
        <template #cell(bonus)="data">
          {{ formatMoney(data.item.bonus, 0, 2) }} VNĐ
        </template>
        <template #cell(thanhtien)="data">
          {{ formatMoney(data.item.thanhtien, 0, 2) }} VNĐ
        </template>
        <template #cell(key)="data">
          <button
            @click.prevent="
              chitiet_hoadon_item_selected = JSON.parse(
                JSON.stringify(data.item)
              );
              show_popup_sua = true;
            "
            class="btn btn-info"
          >
            Sửa
          </button>
          <button
            @click.prevent="deleteHoadon(data.item.key)"
            class="btn btn-danger ml-2"
          >
            Xóa
          </button>
        </template>
      </b-table>
      <b-pagination
        v-model="currentPage"
        :total-rows="danh_sach_chi_tiet_hoa_don.length"
        :per-page="perPage"
        aria-controls="my-table"
      ></b-pagination>
    </div>
    <div>
      <b-button @click="$router.back()" variant="primary">Trở lại</b-button>
    </div>
  </div>
</template>

<script>
import { db } from "../../firebaseDb";
import themmoi from "./themmoi.vue";
import sua from "./sua.vue";

export default {
  data() {
    return {
      show_popup_them_moi: false,
      show_popup_sua: false,
      filter: null,
      perPage: 20,
      currentPage: 1,
      danh_sach_chi_tiet_hoa_don: [],
      fields_table: [
        {
          key: "stt",
          label: "STT",
          sortable: true,
        },
        {
          key: "tensp",
          label: "Tên SP",
          sortable: true,
        },
        {
          key: "soluong",
          label: "Số lượng",
          sortable: true,
        },
        {
          key: "giasi",
          label: "Giá sỉ shop",
          sortable: true,
        },
        {
          key: "giack",
          label: "Giá CK",
          sortable: true,
        },
        {
          key: "gianhap",
          label: "Giá nhập",
          sortable: true,
        },
        {
          key: "bonus",
          label: "Tiền bonus",
          sortable: true,
        },
        {
          key: "thanhtien",
          label: "Thành tiền",
          sortable: true,
        },
        {
          key: "key",
          label: "Hành động",
          sortable: true,
        },
      ],
      chitiet_hoadon_item_selected: {},
    };
  },
  components: {
    themmoi,
    sua,
  },
  created() {
    db.collection("chitiethoadon")
      .where("mahd", "==", this.$route.params.id)
      .onSnapshot((snapshotChange) => {
        this.danh_sach_chi_tiet_hoa_don = [];
        let index = 0;
        snapshotChange.forEach((doc) => {
          index++;
          this.danh_sach_chi_tiet_hoa_don.push({
            key: doc.id,
            stt: index,
            mahd: doc.data().mahd,
            tensp: doc.data().tensp,
            giack: doc.data().giack,
            gianhap: doc.data().gianhap,
            giasi: doc.data().giasi,
            soluong: doc.data().soluong,
            thanhtien: doc.data().thanhtien,
            bonus: doc.data().bonus,
          });
        });
      });
  },
  methods: {
    deleteHoadon(id) {
      if (window.confirm("Bạn có chắc chắn muốn xóa không?")) {
        db.collection("chitiethoadon")
          .doc(id)
          .delete()
          .then(() => {
            console.log("Đã xóa hóa đơn!");
          })
          .catch((error) => {
            console.error(error);
          });
      }
    },
    formatMoney(number, min, max) {
      let num_check = parseFloat(number);
      if (isNaN(num_check)) return number;
      number = parseFloat(number);
      return number.toLocaleString("en", {
        minimumFractionDigits: min,
        maximumFractionDigits: max,
      });
    },
  },
};
</script>

<style>
.btn-primary {
  margin-right: 12px;
}
.table-hoadon td {
  white-space: nowrap;
}
</style>
