<template>
  <div class="row justify-content-center">
    <div class="">
      <h3 class="text-center">Thêm mới sản phẩm vào hóa đơn</h3>
      <form @submit.prevent="themmoi">
        <div class="form-group m-2">
          <label>Tên sản phẩm</label>
          <input
            type="text"
            class="form-control"
            v-model="chitiethoadon.tensp"
            required
          />
        </div>
        <div class="form-group m-2">
          <label>Số lượng</label>
          <input
            type="number"
            class="form-control"
            v-model="chitiethoadon.soluong"
            @keydown="update_gia"
            required
          />
        </div>
        <div class="form-group m-2">
          <label>Giá sỉ</label>
          <input
            type="number"
            class="form-control"
            v-model="chitiethoadon.giasi"
            @keydown="update_gia"
            required
          />
        </div>
        <div class="form-group m-2">
          <label>Giá chiết khấu</label>
          <input
            type="number"
            class="form-control"
            v-model="chitiethoadon.giack"
            required
          />
        </div>
        <div class="form-group m-2">
          <label>Giá nhập</label>
          <input
            type="number"
            class="form-control"
            v-model="chitiethoadon.gianhap"
            @keydown="update_gia"
            required
          />
        </div>
        <div class="form-group m-2">
          <label>Bonus</label>
          <input
            type="number"
            class="form-control"
            v-model="chitiethoadon.bonus"
            required
          />
        </div>
        <div class="form-group m-2">
          <label>Thành tiền</label>
          <input
            type="number"
            class="form-control"
            v-model="chitiethoadon.thanhtien"
            required
          />
        </div>
        <div class="form-group mt-2 mt-2">
          <button class="btn btn-success btn-block">Thêm mới</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { db } from "../../firebaseDb";

export default {
  data() {
    return {
      chitiethoadon: {
        mahd: "",
        tensp: "",
        giack: 0,
        gianhap: 0,
        giasi: 0,
        soluong: 0,
        thanhtien: 0,
        bonus: 0,
      },
    };
  },
  methods: {
    themmoi(e) {
      e.preventDefault();
      this.chitiethoadon.mahd = this.$route.params.id;
      db.collection("chitiethoadon")
        .add(this.chitiethoadon)
        .then(() => {
          alert("Thêm mới thành công!");
          this.chitiethoadon = {
            mahd: "",
            tensp: "",
            giack: 0,
            gianhap: 0,
            giasi: 0,
            soluong: 0,
            thanhtien: 0,
            bonus: 0,
          };
        })
        .catch((error) => {
          alert("Có lỗi xảy ra vui lòng thử lại sau!");
          console.log(error);
        });
    },
    update_gia() {
      setTimeout(() => {
        this.chitiethoadon.thanhtien =
          parseFloat(this.chitiethoadon.soluong) *
          parseFloat(this.chitiethoadon.giasi);
        this.chitiethoadon.bonus =
          parseFloat(this.chitiethoadon.soluong) *
            parseFloat(this.chitiethoadon.giasi) -
          parseFloat(this.chitiethoadon.soluong) *
            parseFloat(this.chitiethoadon.gianhap);
      }, 500);
    },
  },
};
</script>
